.App {
  position: absolute;
  max-height: 100%;
  height: 100%;
  max-width: 100%;
  width: 100%;
  text-align: center;
  background: rgb(132,93,161);
  background: radial-gradient(circle, rgba(132,93,161,1) 0%, rgba(20,25,32,1) 85%);
}

.App-logo {
  height: 40vmin;
}

.App-header {
  min-height: 0vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 100%;
  color: white;
}

.text {
  position: fixed;
	top: 50%;
	right: 50%;
	transform: translate(50%,-50%);
	color: $text;
  max-width: 90%;
  max-height: 90%;
  width: inherit;
  height: inherit;
	padding: 2em 3em;
  background: rgba(0, 0, 0, 0.2);
  text-shadow: 0px 0px 2px #131415;
	font-family: "Lucida Console", Monaco, monospace;
}

.particles {
  height: 100%;
  width: 100%;
}